'use client';

import { ReactNode, Suspense, useEffect } from 'react';

import { WithChildren, useHistory } from '@/shared/lib/index.js';

interface Props extends WithChildren {
    defaultPrimaryPage?: string;
}

export const SecondaryPage = ({ children, defaultPrimaryPage = '/' }: Props): ReactNode => {
    const history = useHistory();
    useEffect(() => {
        if (!history?.state || history?.state?.isLocationNotEqual(window.location.href)) {
            const primaryPage = history?.state?.data.primaryPage ?? { pathname: defaultPrimaryPage, query: '' };
            history?.pushState({
                href: window.location.href,
                primaryPage
            });
        }
        // хук должен отработать один раз только при загрузке страницы
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    return <Suspense>{children}</Suspense>;
};
