'use client';

import { observer } from 'mobx-react-lite';
import { ReactNode } from 'react';

import { WelcomePage } from '@/pages/welcome/index.js';

import { useStore, WithChildren, Experiments } from '@/shared/lib/index.js';

interface Props extends WithChildren {}

export const WithBetaUser = observer(({ children }: Props): ReactNode => {
    const { user, experiments } = useStore();

    if (user.isAuthorized) {
        if (experiments.checkExperiment(Experiments.WebNext, 'on') && !user.isChild) {
            if (user.hasPlus) {
                return children;
            }

            return <WelcomePage withOffer showUserProfile />;
        }

        return <WelcomePage withoutExperiment showUserProfile />;
    }

    return <WelcomePage showLoginButton />;
});
