'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useCallback, useMemo } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { LogoNew } from '@yandex-music-int/sol/components/LogoNew';
import { SROnly } from '@yandex-music-int/sol/components/SROnly';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';

import { UserProfile } from '@/entities/user/index.js';

import {
    CONTAINER_AUTHORIZATION,
    CONTAINER_CONFIG_TOKEN,
    LayoutName,
    useContainer,
    useStore,
    useNavigate,
    useRumPage,
    getTLDHost,
    useUrl
} from '@/shared/lib/index.js';
import { Link } from '@/shared/ui/index.js';

import styles from './Welcome.module.scss';

interface Props {
    withoutExperiment?: boolean;
    showLoginButton?: boolean;
    showUserProfile?: boolean;
    withOffer?: boolean;
}

export const WelcomePage = observer(
    ({
        withoutExperiment = false,
        showLoginButton = false,
        showUserProfile = false,
        withOffer = false
    }: Props): ReactElement => {
        const container = useContainer();
        const { settings } = useStore();
        const { tld } = useUrl();
        const { formatMessage } = useIntl();
        const authorization = container.get(CONTAINER_AUTHORIZATION);
        const navigate = useNavigate();
        const config = container.get(CONTAINER_CONFIG_TOKEN);
        const oldWebHost = getTLDHost(config.oldWebHost, tld);

        const backgroundUrl = useMemo(() => {
            if (settings.layout === LayoutName.Mobile) {
                // eslint-disable-next-line @typescript-eslint/no-var-requires
                return require('./assets/mobile.webp').default.src;
            }
            // eslint-disable-next-line @typescript-eslint/no-var-requires
            return require('./assets/desktop.webp').default.src;
        }, [settings.layout]);

        const handleLogin = useCallback(() => {
            if (authorization.authorizationUrl) {
                navigate(authorization.authorizationUrl);
            }
        }, [navigate, authorization.authorizationUrl]);

        useRumPage(true);

        const headerComponent = useMemo(() => {
            if (withOffer) {
                return (
                    <Heading variant="h1" size="xl" weight="bold" className={clsx(styles.title, styles.text)}>
                        <FormattedMessage id="welcome-page.offer-header" />
                    </Heading>
                );
            }

            if (withoutExperiment) {
                return (
                    <Heading variant="h1" size="xxl" weight="bold" className={clsx(styles.title, styles.text)}>
                        <FormattedMessage id="welcome-page.beta-header" values={{ br: <br /> }} />
                    </Heading>
                );
            }

            return (
                <Heading variant="h1" size="xxl" weight="bold" className={clsx(styles.title, styles.text)}>
                    <FormattedMessage id="welcome-page.not-auth-header" values={{ br: <br /> }} />
                </Heading>
            );
        }, [withOffer, withoutExperiment]);

        const textComponent = useMemo(() => {
            if (withoutExperiment) {
                return (
                    <>
                        <Caption variant="span" type="text" size="l" className={styles.text}>
                            <FormattedMessage id="welcome-page.beta-text-short" />
                        </Caption>
                        <SROnly>
                            <Heading variant="h2">
                                <FormattedMessage id="welcome-page.beta-text-short" />
                            </Heading>
                        </SROnly>
                    </>
                );
            }

            if (withOffer) {
                return (
                    <>
                        <Caption variant="span" type="text" size="l" className={styles.offerText}>
                            <FormattedMessage id="welcome-page.offer-text" />
                        </Caption>
                        <SROnly>
                            <Heading variant="h2">
                                <FormattedMessage id="welcome-page.offer-text" />
                            </Heading>
                        </SROnly>
                    </>
                );
            }

            return (
                <>
                    <Caption variant="span" type="text" size="l" className={styles.text}>
                        <FormattedMessage id="welcome-page.not-auth-text" values={{ br: <br /> }} />
                    </Caption>
                    <SROnly>
                        <Heading variant="h2">
                            <FormattedMessage id="welcome-page.not-auth-text" values={{ br: <br /> }} />
                        </Heading>
                    </SROnly>
                </>
            );
        }, [withOffer, withoutExperiment]);

        const buttonComponent = useMemo(() => {
            if (showLoginButton) {
                return (
                    <Button
                        onClick={handleLogin}
                        className={styles.loginButton}
                        role="link"
                        color="primary"
                        size="m"
                        radius="xxxl"
                    >
                        <FormattedMessage id="authorization.enter-button" />
                    </Button>
                );
            }

            if (withOffer) {
                const payPageUrl = `https://${oldWebHost}/pay`;

                return (
                    <Link
                        className={styles.offerButtonLink}
                        href={payPageUrl}
                        aria-label={formatMessage({ id: 'payment.offer-button' })}
                        target="_blank"
                    >
                        <Button
                            className={clsx(styles.offerButton, styles.offerButton_important)}
                            role="link"
                            color="primary"
                            size="m"
                            radius="m"
                            withRipple={false}
                        >
                            <FormattedMessage id="payment.offer-button" />
                        </Button>
                    </Link>
                );
            }

            return;
        }, [formatMessage, handleLogin, oldWebHost, showLoginButton, withOffer]);

        return (
            <div className={clsx(styles.root)}>
                <div className={styles.logoContainer}>
                    <LogoNew alignIcon="center" lang="ru" className={styles.logo} />
                </div>
                {headerComponent}
                {textComponent}
                {buttonComponent}

                <img className={styles.image} src={backgroundUrl} />

                {showUserProfile && (
                    <UserProfile
                        className={styles.user}
                        withMeta={settings.layout !== LayoutName.Mobile}
                        variant={settings.layout === LayoutName.Mobile ? 'mobile' : 'desktop'}
                    />
                )}
            </div>
        );
    }
);
