'use client';

import { observer } from 'mobx-react-lite';
import { ReactNode } from 'react';

import { Login } from '@/widgets/login/index.js';

import { useStore, WithChildren } from '@/shared/lib/index.js';

interface Props extends WithChildren {}

export const WithAuth = observer(({ children }: Props): ReactNode => {
    const { user } = useStore();

    if (user.isAuthorized) {
        return children;
    }

    return <Login />;
});
