'use client';

import { clsx } from 'clsx';
import { ReactElement, useEffect, useRef } from 'react';

import { useToggle } from '@yandex-music-int/sol/common/hooks/useToggle.js';

import { WithChildren } from '@/shared/lib/index.js';

import styles from './WithSplashScreen.module.scss';

interface Props extends WithChildren {}

export const WithSplashScreen = ({ children }: Props): ReactElement => {
    const assetPrefix = process.env.ASSET_PREFIX || '';
    const { state: showSplashScreen, toggleFalse: removeSplashScreen } = useToggle(true);
    const { state: splashScreenVisible, toggleTrue: visibleSplashScreen } = useToggle(false);
    const { state: splashScreenHidden, toggleTrue: hideSplashScreen } = useToggle(false);

    const ref = useRef<HTMLVideoElement | null>(null);

    useEffect(() => {
        visibleSplashScreen();
        ref.current?.play();
    }, [visibleSplashScreen]);

    return (
        <>
            {showSplashScreen && (
                <div
                    className={clsx(styles.root, {
                        [styles.root_hidden]: splashScreenHidden
                    })}
                    onAnimationEnd={removeSplashScreen}
                >
                    <video
                        preload="auto"
                        className={clsx(styles.video, {
                            [styles.video_visible]: splashScreenVisible
                        })}
                        muted
                        ref={ref}
                        onEnded={hideSplashScreen}
                        playsInline
                    >
                        <source src={`${assetPrefix}/video/splash_screen/splash_screen.webm`} type="video/webm" />
                        <source src={`${assetPrefix}/video/splash_screen/splash_screen.mp4`} type="video/mp4" />
                    </video>
                </div>
            )}
            {children}
        </>
    );
};
