'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useCallback } from 'react';
import { FormattedMessage } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Heading, Caption } from '@yandex-music-int/sol/components/Typography';

import { CONTAINER_AUTHORIZATION, useContainer, useNavigate } from '@/shared/lib/index.js';

import styles from './Login.module.scss';

export const Login = observer((): ReactElement => {
    const container = useContainer();
    const authorization = container.get(CONTAINER_AUTHORIZATION);
    const navigate = useNavigate();

    const handleLogin = useCallback(() => {
        if (authorization.authorizationUrl) {
            navigate(authorization.authorizationUrl);
        }
    }, [navigate, authorization.authorizationUrl]);

    return (
        <div className={styles.root}>
            <Heading className={clsx(styles.title, styles.important)} variant="h3" size="xs">
                <FormattedMessage id="authorization.enter-title" />
            </Heading>

            <Caption
                className={clsx(styles.text, styles.important)}
                variant="span"
                type="text"
                size="l"
                weight="normal"
            >
                <FormattedMessage id="authorization.enter-text" />
            </Caption>

            <Button onClick={handleLogin} className={styles.button} role="link" color="primary" size="l" radius="xxxl">
                <Caption type="controls" variant="span" size="m">
                    <FormattedMessage id="authorization.enter-button" />
                </Caption>
            </Button>
        </div>
    );
});
